.forecast_window {
  
    width: 415px;
    height: 130px;
    display:flex;
    align-items: center;
    margin: auto auto;
    // position: relative;

    .shrinkImage{
        transform: scale(0.7);
    }
    img {
        transition: 0.75s;
        transform: scale(1.2);
        cursor: pointer;
    }

    .left_arrow {
        background: url('../../../assets/left_arrow.png');
        background-size: cover;
        border: none;
      
        position:relative;
        cursor: pointer;
      
        right:7px;
        height: 25px;
        width: 15px;
      }

    .right_arrow {
        background: url('../../../assets/left_arrow.png');
        background-size: cover;
        border: none;
      
        transform: scalex(-1);
        position:relative;
        cursor: pointer;
        left:7px;
        top: 5x;
        height: 25px;
        width: 15px;
      }

      .left_arrow:hover, 
      .right_arrow:hover {
        filter: drop-shadow(0px 0px 6px rgb(126, 153, 64));
      }
  }

  .forecast_slider {
    
    // position: relative;
    border-radius: 12px;
    // background-color: #1F1F1F;
    color: #ffffff;
  
    overflow:hidden;
    float:right;

    .forecast_container {
        display: flex;
        width: 700px;
        height: 130px;
        margin-top: 20px;
        align-items: center;
        text-align: center;
      
        transition: transform 500ms;
        

        span {
            // margin-bottom: 4px;  
            margin-top:10px;
            font-family: 'Arvo', serif;
            font-size:  18px;
            // width: 8ch;
          }

        .folderElement {
            margin-left: 12px;
            margin-right: 15px;
        }
      }
  }

  .decriptionPanel {
    position: absolute;
    display: flex;
    // position: fixed !important;
    width: 375px;
    height: 110px;
    inset: 0;
    top: -160px;
    left: 0;
    margin-top: -20px;
    background-color: aliceblue;
    border: 10px solid rgba(255, 255, 255, 0.822);
    color: black;
    z-index: 999 !important;
    transition: 1.5s;

    .imageContainer {
      display: inline-block;
      width: 115px;
      height: 130px;
      isolation: isolate;
      color: aliceblue;

      &:before {
        position: absolute;
        content: "";
        inset: 0;
        width: 110px;
        height: 40px;
        background-color: black;
        border-radius: 12px;

        z-index: -1;
        background:
         linear-gradient(
           0deg,
           rgba(0 0 0 / 0.6),
           rgba(0 0 0 / 0.3)
         )
      }

      > span{
        position: relative;
        font-size: 12px;
        height: 50px;
        min-width: 110px;
      }
      .image {
        position: absolute;
        bottom: 0;
        width: 90px;
        height: 80px;
        background: url('../../../assets/folderOpen.png');
        background-size: cover;

        > div {
          position: absolute;
          content: "";
          bottom: -24px;
          left: 175px;
          width: 31px;
          height: 31px;
          background-color: aliceblue;
          z-index: -1;
          cursor: pointer;
          border-collapse: separate;
          transform: rotate(45deg);
          img {
            margin-top: 6px;
            margin-left: 6px;
            transform: rotate(-45deg);
            
          }
        }
      };
    }

    .description {
      width: 70%;

      .filesGrid {
        width: 100%;
        display: flex;
        flex-flow: row wrap;
        justify-content: flex-start;
      }
      .filesGrid > a {
        // flex: 0 0 calc(25% - 1em);
        min-height: 25px;
        width: 130px;
        // max-width: 120px;
        // margin: 0.1em 0.5em;

        img {
          height: 12px;
          margin-right: 5px;
        }

         span {
          width: 11ch !important;
          font-size: 14px;
        }
      }
    }
  }
  .decriptionPanel2 {
    // position: absolute;
    position: fixed !important;
    width: auto;
    height: auto;
    top: -100px;
    background-color: aliceblue;
    border: 10px solid rgba(255, 255, 255, 0.822);
    color: black;
    z-index: 999 !important;
    box-shadow: 2px 3px 30px 9px rgba(255, 255, 255, 0.25);
  
    border-radius: 10px 10px 10px 10px;
    transition: 1s;
  }

  @keyframes containerFadeIn {
    from {
      transform: translateX(2000px);
    }
  
    to {
      transform: translateX(0);
    }
  }

  .description.removeDropDown {
    transition: 1s;
    transform: translateY(-400px);
  }